/* Header styles */
.header {
    background-color: #282c34;
    padding: 20px;
    color: white;
    display: grid;
    grid-template-columns: 1fr auto;
    justify-items: center; /* Center content by default */
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 1000;
  }
  
  .header .title {
    font-size: 2rem;
    text-align: center;
    grid-column: 1 / 2; /* Center title in large screens */
  }
  
  .nav {
    list-style: none;
    padding: 0;
    display: flex;
    justify-content: flex-end;
    gap: 20px;
    margin: 0;
  }
  
  .nav-item {
    margin: 0;
  }
  
  .nav-link {
    color: white;
    text-decoration: none;
    font-size: 16px;
    padding: 5px 10px;
    border-radius: 4px;
    transition: background-color 0.3s ease;
  }
  
  .active-nav-link {
    font-weight: bold;
    text-decoration: underline;
    background-color: #61dafb;
  }
  
  /* Hamburger menu styles */
  .hamburger {
    display: none;
    background: none;
    border: none;
    cursor: pointer;
    font-size: 30px;
    color: white;
  }
  
  .hamburger:focus {
    outline: none;
  }
  
  .hamburger-icon {
    width: 30px;
    height: 3px;
    background-color: white;
    margin: 6px 0;
    transition: 0.4s;
  }
  
  .nav-mobile {
    display: none;
    list-style: none;
    padding: 0;
    margin: 0;
    flex-direction: column;
    gap: 10px;
    background-color: #282c34;
    text-align: center;
  }
  
  .nav-mobile.active {
    display: flex;
    width: 100%;
  }
  
  /* For smaller screens */
  @media (max-width: 768px) {
    .header {
      grid-template-columns: 1fr auto; /* Keep title on the left */
      justify-items: flex-start; /* Align title to left on smaller screens */
    }
  
    .header .title {
      text-align: left;
    }
  
    .nav {
      display: none; /* Hide normal nav on small screens */
    }
  
    .hamburger {
      display: block; /* Show hamburger icon */
    }
  
    .nav-mobile {
      display: none;
      width: 100%;
    }
  
    .nav-mobile.active {
      display: flex;
    }
  }
  