/* General app styling */
.app {
  display: flex;
  flex-direction: column;
  height: 100vh;
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
}

/* Header styling */
.header {
  background-color: #282c34;
  color: white;
  padding: 1rem;
  text-align: center;
  flex-shrink: 0;
}

/* Main content styling */
.content {
  flex-grow: 1; /* Makes content take up available space */
  padding: 1rem;
  background-color: #f5f5f5;
  overflow-y: auto;
}

/* Footer styling */
.footer {
  background-color: #282c34;
  color: white;
  text-align: center;
  padding: 1rem;
  flex-shrink: 0;
}
