/* Projects.css */
.projects-section {
  padding: 20px;
  text-align: center;
}

.projects-heading {
  font-size: 2em;
  margin-bottom: 20px;
  position: relative;
  display: inline-block;
}

.projects-heading::after {
  content: '';
  position: absolute;
  width: 50%;
  height: 2px;
  background-color: #007bff;
  bottom: 0;
  left: 25%;
}

.projects-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin: 0 10px; /* Space from left and right sides */
}

/* Ensure each card takes up the full width of the container */
.project-card {
  width: 100%; /* Full width of the container */
  padding: 15px;
  border: 1px solid #ccc;
  border-radius: 8px;
  text-align: left;
  position: relative;
  background-color: transparent; /* Transparent background */
  transition: transform 0.3s ease-in-out; /* Smooth transform effect */
}

.project-card:hover {
  transform: scale(1.05); /* Slight scale effect on hover */
}

.project-name {
  cursor: pointer;
  font-weight: bold;
  color: #282c34;
  position: relative;
}

.underline {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #007bff;
  transition: width 0.3s ease;
}

.project-name:hover .underline,
.project-name:focus .underline {
  width: 100%; /* Expand underline on hover/focus */
}

.project-name .underline.expanded {
  width: 100%; /* Expand underline when project is clicked */
}

.project-details {
  margin-top: 10px;
  padding: 10px;
  background-color: #f9f9f9;
  border-radius: 5px;
}

.project-description {
  margin-top: 10px;
}

/* Project Links Styling */
a {
  color: #007bff;
  text-decoration: none;
}

a:hover {
  text-decoration: underline;
}

.project-details p a {
  display: inline-block;
  margin-top: 5px;
  color: #007bff;
}

.project-details p a:hover {
  text-decoration: underline;
}


/* For large screens: Expand all project cards */
@media (min-width: 768px) {
  .project-card {
    display: block; /* Display all projects expanded */
  }

  .project-card.open {
    display: block; /* Show all projects expanded */
  }
}

/* For small screens: Show only one expanded card */
@media (max-width: 768px) {
  .project-card {
    display: block; /* Full width on small screens */
  }

  .project-details {
    display: none; /* Hide project details by default */
  }

  .project-card.open .project-details {
    display: block; /* Show details only for the expanded project */
  }
}
