/* General content styling */
.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px;
  transition: all 0.3s ease-in-out;
  background: linear-gradient(135deg, #00aaff, #ff7f50); /* Bright gradient background */
  background-size: 400% 400%; /* For animation effect */
  animation: gradientBG 8s ease infinite; /* Gradient animation */
}

/* Gradient Background Animation */
@keyframes gradientBG {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

/* Styles for small screens */
.content-small {
  display: block;
}

.content-small > * {
  margin-bottom: 20px;
  transition: all 0.3s ease-in-out;
}

/* Hover effects for content on small screens */
.content-small > *:hover {
  transform: scale(1.05);
  box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
  cursor: pointer;
  background-color: #ffdb58; /* Bright hover color */
}

/* Styles for large screens */
.content-large {
  display: none;
}

@media (min-width: 768px) {
  .content-small {
    display: none;
  }

  .content-large {
    display: block;
  }

  .content-large > * {
    margin-bottom: 30px;
    padding: 20px;
    border-radius: 10px;
    background-color: #ffffff;
    transition: transform 0.3s ease, box-shadow 0.3s ease;
  }

  .content-large > *:hover {
    transform: scale(1.05);
    box-shadow: 0 8px 16px rgba(0, 0, 0, 0.2);
    cursor: pointer;
    background-color: #ffdb58; /* Bright hover color */
  }

  .content-large > .category-container {
    border: 1px solid #ffa500; /* Orange border for visibility */
    background-color: #f4f4f4;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    padding: 20px;
    border-radius: 8px;
    transition: all 0.3s ease;
  }

  .content-large > .category-container:hover {
    transform: translateY(-5px);
    box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  }
}

/* Interactive sections like skill sets */
.category-container {
  width: 100%;
  margin-bottom: 20px;
  background-color: #f4f4f4;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

/* On hover, scale up the category container */
.category-container:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
  background-color: #ffebcd; /* Light color for hover */
}

/* Adding a background color transition effect */
.skills-list-container {
  display: flex;
  flex-direction: column;
  gap: 20px;
  transition: background-color 0.3s ease;
}

.skills-list-container:hover {
  background-color: #f0f8ff; /* Light blue background on hover */
}

/* Skill item styling */
.skill-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #ffffff;
  padding: 10px;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
  transition: transform 0.3s ease, box-shadow 0.3s ease;
  cursor: pointer;
}

.skill-item:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.2);
  background-color: #ffdb58; /* Bright hover color */
}

/* Adding a badge-like effect on skill item names */
.skill-name {
  position: absolute;
  top: -20px;
  background-color: #ff6347; /* Tomato red for visibility */
  color: white;
  padding: 5px 10px;
  border-radius: 5px;
  font-size: 0.9em;
  font-weight: bold;
  white-space: nowrap;
  transform: translateY(-50%);
  opacity: 0;
  pointer-events: none;
  transition: opacity 0.3s ease, transform 0.3s ease;
}

.skill-item:hover .skill-name {
  opacity: 1;
  transform: translateY(-100%);
}

/* Adding animations to skill icons */
.skill-icon {
  width: 60px;
  height: 60px;
  object-fit: contain;
  transition: transform 0.3s ease;
}

.skill-item:hover .skill-icon {
  transform: rotate(15deg);
}

/* Footer Section - Interactive effects */
footer {
  margin-top: 40px;
  text-align: center;
  padding: 20px;
  background-color: #282c34; /* Dark footer background */
  color: #fff;
  border-radius: 8px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

footer:hover {
  transform: translateY(-5px);
  box-shadow: 0 6px 12px rgba(0, 0, 0, 0.15);
}

footer a {
  color: #fff;
  text-decoration: none;
  margin: 0 10px;
}

footer a:hover {
  color: #ff6347; /* Tomato red for footer link hover */
}
