.certificates-section {
  padding: 20px;
  background-color: transparent; /* Ensure the section background stays transparent */
}

.section-heading {
  text-align: center;
  font-size: 2em;
  margin-bottom: 20px;
  font-weight: bold;
  position: relative;
}

.section-heading::after {
  content: '';
  position: absolute;
  width: 50%;
  height: 2px;
  background-color: #007bff;
  bottom: 0;
  left: 25%;
}

/* List styling for certificates */
.certificates-list {
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Styling for each certificate card */
.certificate-card {
  border: 1px solid #ddd;
  border-radius: 8px;
  padding: 15px;
  background-color: transparent; /* Transparent background for the card */
  cursor: pointer;
  position: relative;
  transition: transform 0.3s ease, box-shadow 0.3s ease;
}

.certificate-card:hover {
  transform: scale(1.05); /* Slight scale effect on hover */
  box-shadow: 0 4px 10px rgba(0, 123, 255, 0.2); /* Subtle shadow on hover */
}

.course-name {
  font-size: 1.5em;
  margin: 0 0 10px 0;
  color: #333;
  position: relative;
}

.underline {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 0;
  height: 2px;
  background-color: #007bff;
  transition: width 0.3s ease;
}

.course-name:hover .underline,
.course-name:focus .underline {
  width: 100%; /* Expand underline on hover/focus */
}

.course-name .underline.expanded {
  width: 100%; /* Expand underline when project is clicked */
}

.certificate-details {
  margin-top: 10px;
}

.platform {
  font-style: italic;
  color: #555;
}

.link {
  display: inline-block;
  margin: 10px 0;
  color: #007bff;
  text-decoration: none;
  transition: color 0.3s ease;
}

.link:hover {
  color: #0056b3; /* Darker shade on hover */
}

.topics-list {
  list-style-type: disc;
  padding-left: 20px;
}

/* Hide certificate details by default on small screens */
@media (max-width: 768px) {
  .certificate-card {
    width: 100%;
  }

  .certificate-details {
    display: none; /* Hide certificate details by default on small screens */
  }

  .certificate-card.open .certificate-details {
    display: block; /* Show certificate details when the card is expanded */
  }
}
