/* General styling */
.about-section {
  padding: 20px;
  background-color: transparent; /* Set background to transparent */
  border-radius: 10px; /* Rounded corners */
  max-width: 1200px; /* Maximum width of the section */
  margin: 0 auto; /* Center the section horizontally */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1); /* Subtle shadow for depth */
}

.about-content {
  display: flex; /* Flexbox for alignment */
  justify-content: space-between; /* Space elements between each other */
  align-items: center; /* Vertically center elements */
  transition: transform 0.7s ease-out; /* Smooth transform animation */
  margin-bottom: 30px; /* Space below the content */
}

.about-text {
  flex: 1; /* Take up remaining space */
  padding-left: 20px; /* Padding to the left */
}

.about-profile {
  flex: 1; /* Take up remaining space */
  display: flex;
  justify-content: center; /* Center profile image horizontally */
  align-items: center; /* Center profile image vertically */
}

.profile-image {
  width: 200px; /* Set image width */
  height: 200px; /* Set image height */
  border-radius: 50%; /* Make image circular */
  object-fit: cover; /* Ensure image covers the circle */
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.2); /* Add shadow around the profile image */
}

/* Profile Icons Box */
.about-profiles {
  text-align: center; /* Center the profile icons */
  margin-top: 30px; /* Space from the content above */
}

.about-icons {
  display: flex; /* Flexbox for icons layout */
  justify-content: center; /* Center icons horizontally */
  gap: 20px; /* Space between icons */
  margin-top: 20px; /* Space above the icons */
}

.icon-link {
  font-size: 1.5em; /* Icon size */
  color: #333; /* Default icon color */
  transition: color 0.3s ease; /* Smooth color transition */
  padding: 10px; /* Padding around the icons */
  border-radius: 50%; /* Circular icon container */
  background-color: transparent; /* Transparent background for icons */
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1); /* Subtle shadow around icons */
}

.icon-link:hover {
  color: #007bff; /* Change color on hover */
  background-color: #f2f2f2; /* Light gray background on hover */
}

.about-profiles h3 {
  font-size: 1.8em; /* Header size */
  margin-bottom: 20px; /* Space below the header */
}

/* Media Queries for Smaller Screens */
@media (max-width: 768px) {
  /* Change layout for smaller screens */
  .about-content {
    flex-direction: column; /* Stack content vertically */
    align-items: center; /* Center items vertically */
  }

  .about-text {
    transform: translateY(50px); /* Initial off-screen position */
    animation: slide-in-left 1s forwards; /* Animation for text sliding in from the left */
  }

  .about-profile {
    transform: translateY(50px); /* Initial off-screen position */
    animation: slide-in-right 1s forwards; /* Animation for profile image sliding in from the right */
  }

  .about-icons {
    flex-direction: row; /* Align icons in a row */
    gap: 15px; /* Space between icons */
    margin-top: 20px; /* Space above icons */
    align-items: center; /* Align icons vertically */
  }

  .about-profile,
  .about-text {
    order: 1; /* Ensure that image is above text on smaller screens */
  }
}

/* Slide-in Animations */
@keyframes slide-in-left {
  0% {
    transform: translateX(-100%); /* Start off-screen to the left */
  }
  100% {
    transform: translateX(0); /* End at original position */
  }
}

@keyframes slide-in-right {
  0% {
    transform: translateX(100%); /* Start off-screen to the right */
  }
  100% {
    transform: translateX(0); /* End at original position */
  }
}

@keyframes slide-in-bottom {
  0% {
    transform: translateY(50px); /* Start below */
  }
  100% {
    transform: translateY(0); /* End at original position */
  }
}
