/* General Section Styling */
.contact-section {
    padding: 40px;
    background-color: transparent; /* Transparent background */
    border-radius: 12px;
    max-width: 900px;
    margin: 0 auto;
    text-align: center;
    margin-bottom: 40px; /* Added margin to provide gap at the end */
  }
  
  .contact-title {
    font-size: 2.5em;
    color: #333;
    margin-bottom: 20px;
    font-weight: 600;
  }
  
  .contact-form {
    display: flex;
    flex-direction: column;
    gap: 15px;
    align-items: center;
  }
  
  .form-group {
    width: 100%;
    max-width: 600px;
    margin-bottom: 15px;
    text-align: left;
  }
  
  .form-label {
    font-size: 1.1em;
    color: #555;
    margin-bottom: 8px;
  }
  
  .form-input,
  .form-textarea {
    padding: 12px 15px;
    font-size: 1em;
    border-radius: 8px;
    border: 1px solid #ddd;
    width: 100%;
    max-width: 600px;
    box-sizing: border-box;
  }
  
  .form-textarea {
    resize: vertical;
  }
  
  .form-button {
    padding: 12px 20px;
    font-size: 1.1em;
    color: #fff;
    background-color: #0073b1;
    border: none;
    border-radius: 8px;
    cursor: pointer;
  }
  
  .form-button:hover {
    background-color: #005f8c;
  }
  
  /* Responsive Design */
  @media (max-width: 768px) {
    .contact-section {
      padding: 30px;
    }
  
    .contact-title {
      font-size: 2em;
    }
  
    .form-input,
    .form-textarea {
      max-width: 100%;
    }
  }
  