/* General Styling */
.education-section {
  padding: 20px;
  background-color: #f3f3f3;
  color: #333;
}

.timeline {
  position: relative;
  margin: 20px 0;
  padding: 0;
}

.timeline-item {
  position: relative;
  margin-bottom: 40px;
  padding-left: 50px;
}

.timeline-dot {
  width: 15px;
  height: 15px;
  background-color: #007bff;
  border: 3px solid #fff;
  border-radius: 50%;
  position: absolute;
  left: 0;
  top: 10px;
  z-index: 10;
}

.timeline-content {
  background-color: #fff;
  border-radius: 8px;
  padding: 15px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  position: relative;
  border-left: 4px solid #007bff;
}

.degree-name {
  font-size: 1.5em;
  cursor: pointer;
  color: #007bff;
}

.degree-name:hover {
  color: #0056b3;
}

.institution {
  font-style: italic;
  color: #666;
}

.details {
  list-style-type: disc;
  padding-left: 20px;
  margin-top: 10px;
}

.details li {
  margin-bottom: 5px;
}

/* Timeline Line */
.timeline::before {
  content: '';
  position: absolute;
  width: 4px;
  background-color: #ddd;
  top: 0;
  bottom: 0;
  left: 7px;
  z-index: 1;
}

/* Responsive Design */
@media (max-width: 768px) {
  .timeline-item {
    padding-left: 30px;
  }

  .timeline-dot {
    left: -5px;
  }

  .timeline-content {
    padding: 10px;
  }

  .degree-name {
    font-size: 1.2em;
  }

  .details {
    font-size: 0.9em;
  }
}
